.slides {
  display: flex;
  justify-content: center;
  align-items: center;
}

.list {
  display: flex;
  padding: 0;
  list-style-type: none;

  & > * {
    margin-right: 32px;
  }
  & > *:last-child {
    margin-right: 0;
  }
}

.navButtons {
  width: 24px;
  height: 24px;
  margin: 0px 32px;
  cursor: pointer;
}

.dotsControls {
  display: block;
  text-align: center;
}

.slides-info {
  text-align: center;
  margin-top: 5px;
}
